@charset "UTF-8";
/* Layout */
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  color: #404A57;
  font-size: 16px; }

/* TITRES --------------------------------------------------------------------------------------- */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-left: 1rem;
  margin-right: 1rem;
  font-weight: 300; }

h1 {
  margin-top: 1rem;
  margin-bottom: 1.25em;
  font-size: 2.5rem;
  color: #339597; }

h2 {
  margin-top: 1rem;
  margin-bottom: 1.25em;
  font-size: 2rem;
  color: #339597; }

h3 {
  margin-top: 1.5em;
  font-size: 1.6rem; }

h4 {
  font-size: 1.5rem;
  margin-left: 1.5em; }

h5 {
  font-size: 1.3rem;
  margin-left: 3em; }

h6 {
  font-size: 1.3rem;
  margin-left: 4.5em; }

/* INLINE --------------------------------------------------------------------------------------- */
p {
  line-height: 1.4em;
  margin: 0.5rem 1rem; }

a {
  color: #339597; }

blockquote {
  border-left: 3px solid #ddd;
  margin-left: 1em;
  padding-left: 1em; }

table {
  border-collapse: collapse; }

tbody tr:nth-child(even) {
  background-color: #fbfbfc; }

td,
th {
  padding: 0.4em 0.8em;
  border: 1px solid #eceef1; }

th {
  border-bottom: 2px solid #dee1e6; }

ul > li {
  list-style-type: square; }

hr {
  margin: 2em 0;
  border: none;
  border-bottom: 1px solid #acb5c2; }

code,
pre {
  font-family: Consolas, Monaco, "Ubuntu Mono", monospace; }
  code[class*="language-"], code[class*="language-"],
  pre[class*="language-"],
  pre[class*="language-"] {
    font-size: 14px; }

:not(pre) > code {
  font-family: "Monaco", monospace;
  font-size: 14px;
  padding: 0 2px;
  border-radius: 2px;
  background-color: #eceef1;
  border: 1px solid #dee1e6;
  color: #6b7c92; }

@font-face {
  font-family: "Open Sans";
  src: url(../../fonts/OpenSans-Light.woff);
  font-weight: 300; }

@font-face {
  font-family: "Open Sans";
  src: url(../../fonts/OpenSans-LightItalic.woff);
  font-weight: 300;
  font-style: italic; }

@font-face {
  font-family: "Open Sans";
  src: url(../../fonts/OpenSans-Regular.woff);
  font-weight: 400; }

@font-face {
  font-family: "Open Sans";
  src: url(../../fonts/OpenSans-Italic.woff);
  font-weight: 400;
  font-style: italic; }

@font-face {
  font-family: "Open Sans";
  src: url(../../fonts/OpenSans-Bold.woff);
  font-weight: 700; }

@font-face {
  font-family: "Open Sans";
  src: url(../../fonts/OpenSans-BoldItalic.woff);
  font-weight: 700;
  font-style: italic; }

@font-face {
  font-family: "Monaco";
  src: url(../../fonts/Monaco_Linux.woff); }

@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(../../fonts/MaterialIcons-Regular.woff2); }

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: 'liga';
  vertical-align: bottom; }

.absent {
  display: none; }

.invisible {
  visibility: hidden; }

.fade-in {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  opacity: 1;
  transition: opacity .2s linear;
  z-index: 2; }

.fade-out {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  opacity: 0;
  height: 0;
  z-index: 1;
  transition: opacity .2s linear;
  transition: height 0 .2s linear; }

.inside,
.outside {
  position: absolute;
  top: 0;
  transition: transform 0.3s ease; }

.inside--left {
  left: 0;
  transform: translateX(0); }

.outside--left {
  left: 0;
  transform: translateX(-110%); }

.inside--right {
  right: 0;
  transform: translateX(0); }

.outside--right {
  right: 0;
  transform: translateX(110%); }

.cliquable {
  cursor: pointer; }

.card {
  background-color: #fff;
  border: 1px solid #dee1e6;
  border-radius: 2px; }

.btn {
  display: inline-block;
  height: 2em;
  line-height: 0;
  padding: 0 8px;
  border-radius: 2px;
  cursor: pointer;
  box-sizing: border-box;
  vertical-align: middle; }
  .btn.btn--default-light {
    border: 1px solid transparent;
    background-color: transparent;
    color: #404A57; }
    .btn.btn--default-light:hover {
      background-color: #fff;
      border: 1px solid #dee1e6; }
    .btn.btn--default-light[disabled] {
      color: #dee1e6; }
  .btn.btn--primary {
    border: none;
    background-color: #40BABD;
    color: #fff; }
    .btn.btn--primary[disabled] {
      opacity: 0.5;
      cursor: auto; }
  .btn.btn--primary-light {
    border: 1px solid #40BABD;
    background-color: #fff;
    color: #40BABD; }
    .btn.btn--primary-light:hover {
      background-color: #40BABD;
      color: #fff; }
    .btn.btn--primary-light[disabled] {
      cursor: auto;
      border-color: #e0e4e9;
      background-color: #fff;
      color: #c3cad3; }
  .btn.btn--text {
    min-width: 6em; }
  .btn i {
    line-height: 30px; }

.success {
  color: #339597; }

.pulse {
  box-shadow: 0 0 0 rgba(64, 186, 189, 0.6);
  animation: pulse 1.5s; }

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(64, 186, 189, 0.8); }
  70% {
    box-shadow: 0 0 2em 1em rgba(64, 186, 189, 0); }
  100% {
    box-shadow: 0 0 0 0 rgba(64, 186, 189, 0); } }

.actions-diaporama {
  display: flex;
  align-items: center;
  justify-content: space-evenly; }

.pagination {
  position: relative;
  cursor: pointer;
  line-height: 24px; }

.toggle {
  margin: 0;
  z-index: 9; }

.masque {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 2; }

.menu-navigation {
  cursor: auto;
  column-width: 15em;
  column-fill: auto;
  font-size: .9em; }
  .menu-navigation li {
    cursor: pointer;
    line-height: 25px; }
    .menu-navigation li.active {
      font-weight: 700; }

.menu-plus,
.liste-docs {
  top: 38px; }

.zone-diapo {
  padding: 1rem;
  position: relative; }

.footer-diapo {
  text-align: center; }

body {
  display: flex;
  flex-direction: row;
  height: 100vh;
  min-width: 960px;
  max-width: 100vw;
  padding: 0 5px;
  background-color: #eceef1;
  font-family: 'Open Sans', 'sans serif';
  font-size: 16px;
  overflow: hidden; }

/* Désactivation des transition lors du chargement de la page */
.preload .colonne-diaporama,
.preload .colonne-pratique {
  -webkit-transition: none !important;
  -moz-transition: none !important;
  -o-transition: none !important;
  transition: none !important; }

.preload .colonne-pratique {
  display: none; }

.zone-actions {
  display: flex;
  flex-basis: 40px;
  min-height: 40px; }

.colonne-diaporama {
  display: flex;
  flex-direction: column;
  flex-basis: 30em;
  margin: 0 5px 5px; }

.colonne-pratique {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 0 5px 5px; }
  .colonne-pratique .zone-actions {
    justify-content: flex-end;
    align-items: center; }
    .colonne-pratique .zone-actions .btn {
      margin-left: 5px; }

.toggle {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1); }
  .toggle ul {
    margin: 0;
    padding: 0; }
    .toggle ul li {
      list-style-type: none; }
    .toggle ul a {
      color: #404A57;
      text-decoration: none; }
    .toggle ul i {
      font-size: 18px;
      margin-right: .5em; }

.menu-navigation {
  height: 100vh;
  box-sizing: border-box;
  border-radius: 0;
  padding: 1rem; }
  .menu-navigation li:before {
    content: "";
    display: inline-block;
    width: 0;
    height: 0;
    margin-right: 0;
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-left: 5px solid transparent;
    transition: margin-right 0.2s ease; }
  .menu-navigation li:hover:before {
    margin-right: 5px; }
  .menu-navigation li.inactif {
    color: #acb5c2; }
    .menu-navigation li.inactif:before {
      content: "lock_outline";
      font-family: "Material icons";
      margin-right: 20px;
      line-height: 0; }
  .menu-navigation li.active {
    font-weight: 700; }
    .menu-navigation li.active:before {
      margin-right: 10px;
      border-left: 5px solid #40BABD; }

.menu-plus li,
.liste-docs li {
  padding: .5rem 1rem; }
  .menu-plus li:hover,
  .liste-docs li:hover {
    background-color: #eceef1; }

.zone-diapo {
  flex: 1;
  position: relative;
  padding: 1rem; }

.footer-diapo {
  padding: 1em; }

.question {
  position: relative;
  margin: 2.5rem 0;
  background-color: #fbfbfc;
  border-top: 1px solid;
  border-bottom: 1px solid;
  border-color: #c6cdd5; }
  .question h3 {
    color: #404A57;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5em;
    margin: 0;
    padding: 0.15em 0.5em;
    background-color: #dee1e6;
    font-style: italic;
    vertical-align: top;
    border-bottom: 1px solid #eceef1; }
    .question h3::after {
